urcesContent":["/// @thanks to raubaca! https://codepen.io/raubaca/pen/BjGKde\n\n$clouds: #ecf0f1;\n$turquoise: #1abc9c;\n$greensea: #16a085;\n$night: #34495e;\n$midnight: #2c3e50;\n$transition: all 0.5s ease;\n$disabled: #747d8c;\n\n.p1-check-trail {\n    display: flex;\n    align-items: center;\n    width: 45px;\n    height: 25px;\n    background: $midnight;\n    border-radius: 2.5em;\n    transition: $transition;\n    cursor: pointer;\n}\n\n.p1-check-handler {\n    display: flex;\n    margin-left: 0.2em;\n    justify-content: center;\n    align-items: center;\n    width: 20px;\n    height: 20px;\n    background: $night;\n    border-radius: 50%;\n    transition: $transition;\n    box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);\n    &:before {\n        content: \"\\00D7\";\n        color: white;\n        font-size: 1em;\n        font-weight: bold;\n    }\n}\n\ninput[type=\"checkbox\"]:checked + .p1-check-trail {\n    background: $greensea;\n    .p1-check-handler {\n        margin-left: 50%;\n        background: $turquoise;\n        &::before {\n            content: \"\\2714\";\n        }\n    }\n}\n\n/* Hide the input */\ninput[type=\"checkbox\"] {\n    position: absolute;\n    opacity: 0;\n    z-index: -1;\n\n    &:disabled ~ .p1-check-trail {\n        background: $disabled;\n        .p1-check-handler {\n            background: $disabled;\n        }\n    }\n}\n\n/**\n *\n *\n *  Switch B begins here\n *\n *\n */\n.p1-switch-b {\n    --line: #505162;\n    --dot: #f7f8ff;\n    --circle: #9ea0be;\n    --duration: 0.3s;\n    --text: #9ea0be;\n    cursor: pointer;\n    input {\n        display: none;\n        & + div {\n            position: relative;\n            &:before,\n            &:after {\n                --s: 1;\n                content: \"\";\n                position: absolute;\n                height: 4px;\n                top: 10px;\n                width: 24px;\n                background: var(--line);\n                transform: scaleX(var(--s));\n                transition: transform var(--duration) ease;\n            }\n            &:before {\n                --s: 0;\n                left: 0;\n                transform-origin: 0 50%;\n                border-radius: 2px 0 0 2px;\n            }\n            &:after {\n                left: 28px;\n                transform-origin: 100% 50%;\n                border-radius: 0 2px 2px 0;\n            }\n            span {\n                padding-left: 56px;\n                line-height: 24px;\n                color: var(--text);\n                &:before {\n                    --x: 0;\n                    --b: var(--circle);\n                    --s: 4px;\n                    content: \"\";\n                    position: absolute;\n                    left: 0;\n                    top: 0;\n                    width: 24px;\n                    height: 24px;\n                    border-radius: 50%;\n                    box-shadow: inset 0 0 0 var(--s) var(--b);\n                    transform: translateX(var(--x));\n                    transition: box-shadow var(--duration) ease, transform var(--duration) ease;\n                }\n                &:not(:empty) {\n                    padding-left: 64px;\n                }\n            }\n        }\n        &:checked {\n            & + div {\n                &:before {\n                    --s: 1;\n                }\n                &:after {\n                    --s: 0;\n                }\n                span {\n                    &:before {\n                        --x: 28px;\n                        --s: 12px;\n                        --b: var(--dot);\n                    }\n                }\n            }\n        }\n    }\n}\n\n/**\n *\n * Switch-C begins here\n *\n */\n\n.p1-switch-c input {\n    position: absolute;\n    opacity: 0;\n}\n\n/**\n   * 1. Adjust this to size\n   */\n\n.p1-switch-c {\n    display: inline-block;\n    font-size: 20px; /* 1 */\n    height: 1em;\n    width: 2em;\n    background: #bdb9a6;\n    border-radius: 1em;\n    cursor: pointer;\n}\n\n.p1-switch-c div {\n    height: 1em;\n    width: 1em;\n    border-radius: 1em;\n    background: #fff;\n    box-shadow: 0 0.1em 0.3em rgba(0, 0, 0, 0.3);\n    -webkit-transition: all 300ms;\n    -moz-transition: all 300ms;\n    transition: all 300ms;\n}\n\n.p1-switch-c input:checked + div {\n    -webkit-transform: translate3d(100%, 0, 0);\n    -moz-transform: translate3d(100%, 0, 0);\n    transform: translate3d(100%, 0, 0);\n}\n\n.p1-switch-c-input {\n    &:disabled ~ .p1-switch-c-slider {\n        background: $disabled;\n    }\n}\n"],"sourceRoot":""}]);
